.gallery-ctr {
    display: block;
    width: 85%;
    max-width: 660px;
    margin-left: 15%;
    text-align: left;
}

.gallery-ctr:not(:hover) .photo-ctr {
    opacity: 1;
}
.gallery-ctr:not(:hover) .photo-img {
    scale: 100%;
}

.photo-ctr {
    width: 120px;
    height: 120px;
    display: inline-block;
    margin: 5px;
}
.photo-ctr:hover {
    cursor: pointer;
    opacity: 1;
    transition: 200ms;
}
.photo-ctr:not(:hover) {
    opacity: 0.6;
    transition: 200ms;
}

.photo-img {
    width: 100%;
    height: 100%;
    /* background-image: url("../images/dghq-thumb.png"); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.photo-img:hover {
    scale: 100%;
    transition: 200ms;
}
.photo-img:not(:hover) {
    scale: 95%;
    transition: 200ms;
}

.view-bg {
    position: fixed;
    z-index: 10000;
    top: 0px;
    left: 0px;
    opacity: 0.9;
    height: 100%;
    width: 100%;
    background: black;
}

.view-ctr {
    
}

.view-exit {
    transition: 200ms;
    position: fixed;
    width: 25px;
    height: 25px;
    padding: 27px;
    z-index: 12000;
    top: 0px;
    right: 0px;
}

.view-exit img {
    width: 100%;
    height: 100%;
}

.view-exit:hover {
    transition: 200ms;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.1);
}

.view-arrow {
    position: fixed;
    top: 80px;
    width: 30px;
    height: 50px;
    opacity: 1;
    z-index: 12000;
    top: 50%;
    transform: translate(0%, -50%);
}

.view-left {
    transition: 200ms;
    left: 0px;
}

.view-left:hover {
    transition: 200ms;
    cursor: pointer;
}

.view-right {
    transition: 200ms;
    right: 0px;
}

.view-right:hover {
    transition: 200ms;
    cursor: pointer;
}

.view-arrow img {
    position: relative;
    width: 16px;
    height: 25px;
    transform: translate(0%, 50%);
}

.view-img {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    width: 87%;
    max-width: 600px;
    max-height: 700px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 11000;
}

@media (min-width: 300px) {
    .gallery-ctr {
        margin-left: 45px;
    }
}

@media (min-width: 600px) {
    .gallery-ctr {
        width: 90%;
    }


}