.site-ctr {
    width: 100%;
}

a {
    text-decoration: none;
}

.site-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}

.site-ctr .site-img {
    padding-left: 40px;
}

.site-img {
    position: relative;
    text-align: left;
}

.site-img img {
    position: relative;
    height: 60px;
    padding: 5px;
    padding-top: 8px;
}

.site-img h2 {
    position: relative;
    display: inline;
    bottom: 28px;
    padding-left: 12px;
    color: white;
    font-weight: 300;
    font-size: 16px;
}