.folder-ctr {
    width: 100%;
}

.port-folder-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}
.web-folder-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}
.art-folder-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}
.proj-folder-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}
.vault-folder-ctr:hover {
    background: #0a0a0a;
    cursor: pointer;
}

.open {
    display: block;
}
.closed {
    display: none;
}

.web-folder-ctr .folder-img {
    padding: 0px 25px;
}
.art-folder-ctr .folder-img {
    padding: 0px 25px;
}
.proj-folder-ctr .folder-img {
    padding: 0px 25px;
}
.vault-folder-ctr .folder-img {
    padding: 0px 25px;
}

.folder-img {
    position: relative;
    text-align: left;
    padding: 0px 10px;
}

.folder-img img {
    position: relative;
    width: 70px;
}

.folder-img h2 {
    position: relative;
    display: inline;
    bottom: 28px;
    padding-left: 10px;
    color: white;
    font-weight: 300;
    font-size: 16px;
}